import React, { forwardRef, useState } from "react";
import { Link, Redirect } from "react-router-dom";
import {
    Button,
    Checkbox,
    ListItemButton,
    ListItemIcon,
    ListItemText,
    Typography,
} from "@mui/material";

import { useLocation } from "react-router-dom";

function Main(_, ref) {
    const [checked, setChecked] = useState(false);
    const { state } = useLocation();

    function handleOnClick() {
        setChecked(v => !v);
    }

    return (
        <React.Fragment>
            {(!state?.location ?? true) && (!state?.address ?? true) && (
                <Redirect to="/" />
            )}
            <div className="title">
                <Typography variant="h5" sx={{ color: "#00192F" }}>
                      
                </Typography>
            </div>
            <Typography variant="body1" sx={{ maxWidth: 280, fontWeight: 100 }}>
                <b>1.  </b>    
                    .
                <br />
                ( ,  )
            </Typography>
            <Typography variant="body1" sx={{ maxWidth: 280, fontWeight: 100 }}>
                <b>2.   </b>     
                .
                <br />
                (    )
            </Typography>
            <Typography variant="body1" sx={{ maxWidth: 280, fontWeight: 100 }}>
                <b>3.  </b>   
                 .
            </Typography>
            <ListItemButton onClick={handleOnClick}>
                <ListItemIcon>
                    <Checkbox checked={checked} />
                </ListItemIcon>
                <ListItemText primary="   ." />
            </ListItemButton>
            <Button
                className="minWidth"
                variant="contained"
                component={Link}
                disabled={!checked}
                to={location => ({
                    pathname: "/pwd",
                    state: {
                        location: location.pathname,
                        address: state?.address,
                    },
                })}>
                
            </Button>
        </React.Fragment>
    );
}

export default forwardRef(Main);
